/* src/Sidebar.css */
.sidebar {
  width: 250px;
  height: 100vh;
  background-color: #333; /* Dark grey */
  color: white;
  padding: 10px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
}

.menu {
  margin-bottom: 20px;
}

.menu-item, .submenu-item, .dropdown-item {
  padding: 10px 20px;
  cursor: pointer;
  background-color: #444; /* Slightly lighter grey */
  margin-bottom: 5px;
  border-radius: 4px;
  transition: background-color 0.3s, transform 0.3s;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-item:hover, .submenu-item:hover, .dropdown-item:hover {
  background-color: #555; /* Even lighter grey */
  transform: translateX(5px);
}

.menu-item.selected, .submenu-item.selected, .dropdown-item.selected {
  background-color: #007bff; /* Highlight color */
  color: white;
}

.dropdown-symbol {
  font-size: 12px;
  margin-left: auto;
}

.submenu {
  margin-left: 20px;
}

.dropdown {
  margin-left: 20px;
  background-color: #555; /* Even lighter grey */
  border-radius: 4px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}

.dropdown-item {
  padding: 10px;
  background-color: #666; /* Light grey */
  border-bottom: 1px solid #555;
}

.dropdown-item:last-child {
  border-bottom: none;
}
