/* src/App.css */
.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f0f0f0; /* Light grey background for contrast */
}

.main {
  display: flex;
  flex: 1;
}

.content {
  flex: 1;
  padding: 20px;
  background-color: #fff;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin: 20px;
}
